.background {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  transition: background 100ms ease;
}

.main {
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 0;
  overflow-y: hidden;
  top: 40vh;
  /*margin: 0 auto; /* Will not center vertically and won't work in IE6/7. */
  left: 0;
  right: 0;
  bottom: 0;
}

.mainInner {
  height: 20vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.status {
  font-size: 10vh;
}

.duration {
  font-size: 10vh;
}

.topBar {
  font-size: 2vmin;
  position: fixed;
  top: 1em;
  margin: 0 auto; /* Will not center vertically and won't work in IE6/7. */
  left: 0;
  right: 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.bottomBar {
  font-size: 2vmin;
  position: fixed;
  bottom: 1em;
  display: flex;
  margin-top: 2em;
  justify-content: space-around;
  align-items: center;
}

.bottomBar ul {
  list-style: none;
  margin-left: 0;
}

.history {
  width: 10em;
  max-height: 10em;
  overflow-y: scroll;
}

.resultsRow {
  display: flex;
}

.resultsRow * {
  display: block;
}

.resultsRow *:first-child {
  flex: 1;
}

